.day-graph-container {
  width: 100%;
  box-sizing: border-box;
  padding: 0.25rem 0.25rem;
  display: flex;
  font-family: Helvetica, Arial, sans-serif;
  position: relative;
}

.day-info {
  width: 25%;
  height: 100%;
  display: flex;
  flex-direction: column;
  font-size: 0.8rem;
  white-space: nowrap;
  justify-content: center;
}

.day-info * {
  margin: 0;
}

.info-row span {
  font-size: 0.85em;
}

.info-row h5 {
  display: inline-block;
  margin-right: 0.25em;
}

.day-graph {
  width: 75%;
  height: 100%;
}

.day-path {
  fill: none;
  stroke: yellowgreen;
  stroke-width: 1px;
}

.time-marker {
  stroke-width: 1px;
  stroke: black;
}


.transition-detail {
  position: absolute;
  bottom: 0;
  right: 0;
  text-align: right;
  margin: 0.5rem;
  display: flex;
  align-items: baseline;
}

.transition-detail span {
  display: block;
  margin: 0.25rem;
}

.op-legend {
  width: 50%;
  position: absolute;
  bottom: 35px;
  right: 0;
  margin: 0.5rem;
}

.op-legend img {
  width: 100%;
}

.ramp-label {
  display: block;
  position: absolute;
  top: -0.75rem;
  font-size: 0.75rem;
}

.desc {
  font-size: 0.75em;
}

.number {
  font-size: 1.5em;
}

.play {
  width: 1.25rem;
  height: 1.25rem;
  border: 1px solid rgba(0,0,0,0.1);
  border-radius: 50%;
  background: whitesmoke 60% 50% / 80% no-repeat url('https://noe-lc.github.io/about-me/icons/play.png');
  padding: 0;
  position: absolute;
  top: 10%;
  right: 5%;
}

.play:hover {
  border-color: rgba(0,0,0,0.5);
}

.play:active {
  top: 12%;
}

.play:focus {
  outline: none;
  border: 1px solid green;
  border-radius: 50%;
}

.scaled {
  transform: scale(1.25,1.25);
}

@media only screen and (max-width:600px) {
  .scaled {
    transform: scale(1.75,1.75);
  }

  .day-graph-container {
    max-height: 100px;
    height: 100% !important;
    flex-shrink: 0;
  }

  .day-info {
    font-size: 1rem;
  }
}

@media only screen and (max-width:900px) {
  .scaled {
    transform: scale(1.75,1.75);
  }

  .transition-detail, .op-legend {
    font-size: 0.8rem;
  }

}