.function-menu {
  width: 97.5%;
  margin: 0 auto;
  font-size: 1.25rem;
}

.var-keyword {
  color: black;
  font-weight: bold;
}

.variable {
  color: #8f72f0;
}

.function-menu h2 {
  font-weight: normal;
  word-spacing: 0.25em;
  margin-bottom: 0.5em;
  text-align: left;
  font-family: monospace;
  word-break: break-all;
  white-space: normal;
}

.function-menu a {
  color: #bdbdbd;
  text-decoration: none;
  margin: 0 0.25em;
  font-style: italic;
}

.function-menu a:hover {
  color: #fd9d69;
  font-style: normal;
}

.function-menu a.selected {
  color: #db692b;
  font-style: normal;
}

@media only screen and (max-width:600px) {
  .function-menu h2 {
    word-break: break-all;
    text-align: center;
  }

  .function-menu a {
    display: block;
  }
}